.bend-down-dropdown-container {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-button {
    cursor: pointer;
    padding: 10px;
    border: 1px solid #ccc;
  }
  
  .dropdown-content {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #fff;
    border: 1px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    overflow: hidden;
    opacity: 0;
    transform: translateY(-10px);
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  
  .bend-down-dropdown-container:hover .dropdown-content {
    opacity: 1;
    transform: translateY(0);
  }
  
  .dropdown-content ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .dropdown-content li {
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  
    &:hover {
      background-color: #f0f0f0;
    }
  }
  