.main-card{
    margin-left: 450px;
}

.card {
    display: flex;
    max-width: 400px;
    margin: 20px;
    padding: 20px;
    border: 2px solid #000; /* Solid black border around the entire card */
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .user-image {
    margin-top: 25px;
    border: #000 solid 2px;
    width: 100px;
    height: 100px;
    border-radius: 8px;
    margin-right: 20px;
  }
  
  .card-details-wrapper {
    display: flex;
    flex-direction: column;
  }
  
  h2 {
    margin-bottom: 10px;
    font-size: 1.2rem;
  }
  
  p {
    margin: 5px 0;
  }
  
